<script>
  import { fade } from "svelte/transition";
  // elements
  import FormGroup from "../Forms/FormGroup.svelte";
  import EmailField from "../Forms/EmailField.svelte";
  import TextField from "../common/FormTextInput.svelte";
  import DatePicker from "./DateField.svelte";
  import TextArea from "./TextArea.svelte";
  import RadioGroup from "./RadioGroup.svelte";
  import YesNoQuestion from "./yesNoQuestion.svelte";
  import Modal from "../common/modal.svelte";
  import SuccessBox from "../common/SuccessBox.svelte";
  import Button from "../common/Button.svelte";
  //  meta
  import { onMount, onDestroy } from "svelte";
  import { Firestore } from "../../config/firebase.js";
  import { push } from "svelte-spa-router";
  import validate from "validate.js";
  // stores

  let ActiveStage = 0,
    teamMemberCount = 0,
    successModalOpen,
    validationerror,
    totalNumberOfStages = 1;
  let stepsElements = document.querySelectorAll("#msform");
  let formLayout = {
    0: {
      title: "Company Overview",
      subtitle: "",
      elements: [
        {
          Name: "Company Name",
          type: "text",
          data: {},
          errorMessege: "This field is required.",
          checkIsValid: input => {
            if (validate.isEmpty(input)) {
              return false;
            } else {
              return true;
            }
          }
        },
        {
          Name: "Idea Overview",
          type: "text",
          multiline: true,
          data: {},
          errorMessege: "This field is required.",
          checkIsValid: input => {
            if (validate.isEmpty(input)) {
              return false;
            } else {
              return true;
            }
          }
        },
        {
          Name: "How far along are you?",
          type: "radio",
          data: {},
          items: ["Idea", "Prototype", "Users", "Paying Users"],
          errorMessege: "This field is required.",
          checkIsValid: input => {
            if (validate.isEmpty(input)) {
              return false;
            } else {
              return true;
            }
          }
        }
      ]
    },
    1: {
      title: "Contact Information",

      elements: [
        {
          Name: "FullName",
          type: "text",

          data: {},
          errorMessege: "This field is required.",
          checkIsValid: input => {
            if (validate.isEmpty(input)) {
              return false;
            } else {
              return true;
            }
          }
        },
        {
          Name: "cellphone",
          type: "text",
          data: {},
          errorMessege: "This field is required.",
          checkIsValid: input => {
            if (validate.isEmpty(input)) {
              return false;
            } else {
              return true;
            }
          }
        },
        {
          Name: "Email",
          type: "email",
          data: {},
          errorMessege: "This field is required.",
          checkIsValid: input => {
            if (validate.isEmpty(input)) {
              return false;
            } else {
              return true;
            }
          }
        }
      ]
    }
  };
  let formData = {
    stagemap: {
      0: "companyInfo",
      1: "contactInfo"
    },
    companyInfo: {},
    contactInfo: {}
  };

  $: {
    Object.keys(formLayout).map(key => {
      let validatedElements = [];
      formLayout[key].elements.map(element => {
        if (
          (element.type === "text") | (element.type === "email") ||
          element.type === "radio"
        ) {
          formData[formData.stagemap[key]][element.Name] =
            element.data["value"];
          // this is a shared property between all inputs in a stage to
          // prevent going to next stage when one doesn't bybass validation
          validatedElements.push(element.data["isValid"]);
        } else if (element.type === "yes-no") {
          formData[formData.stagemap[key]][element.question] =
            element.data["answer"];
          formData[formData.stagemap[key]]["subquestion"] =
            element.data["value"];
          validatedElements.push(element.data["isValid"]);
        } else if (element.type === "member") {
          formData[formData.stagemap[key]][element.index] = {
            name: element.name.value,
            age: element.age.value,
            gender: element.gender.value,
            role: element.role.value,
            city: element.gender.value,
            educationalBackground: element.educationalBackground.value,
            workExperience: element.workExperience.value
          };
          validatedElements.push(element.name.isValid);
          validatedElements.push(element.age.isValid);
          validatedElements.push(element.gender.isValid);
          validatedElements.push(element.role.isValid);
          validatedElements.push(element.gender.isValid);
          validatedElements.push(element.educationalBackground.isValid);
          validatedElements.push(element.workExperience.isValid);
          // validatedElements.concat([
          //   element.name.isValid,
          //   element.age.isValid,
          //   element.gender.isValid,
          //   element.role.isValid,
          //   element.gender.isValid,
          //   element.educationalBackground.isValid,
          //   element.workExperience.isValid
          // ]);
        }
      });
      console.log(validatedElements);
      formData[formData.stagemap[key]].isStageValid =
        validatedElements.findIndex(element => element === false) === -1
          ? true
          : false;
    });
  }


  function sanitizeDataForFirebase(data) {
    Object.keys(data).map(key => {
      if (data[key] === undefined) {
        delete data[key];
      }
      if (typeof data[key] === "object") {
        sanitizeDataForFirebase(data[key]);
      }
    });
  }
  // event handlers and lifecycle methods
  onMount(() => {
    document.querySelector("html").style.backgroundColor =
      " rgb(129, 111, 131)";
  });
  onDestroy(() => {
    document.querySelector("html").style.backgroundColor = "";
  });
  let handleNext = event => {
    // makeSureNotEmpty();
    if (formData[formData.stagemap[ActiveStage]].isStageValid) {
      ActiveStage = (ActiveStage + 1) % 3;
      validationerror = "";
    } else {
      validationerror = true;
    }
  };
  let handlePrevious = params => {
    ActiveStage = (ActiveStage - 1) % 3;
    validationerror = "";
  };
  let handleSubmit = () => {
    // makeSureNotEmpty();
    if (!formData[formData.stagemap[ActiveStage]].isStageValid) {
      return;
    }
    sanitizeDataForFirebase(formData);
    let collection = Firestore.collection("exhipition");
    collection
      .add(formData)
      .then(function() {
        console.log("Document successfully written!");
        successModalOpen = true;
      })
      .catch(function(error) {
        console.error("Error writing document: ", error);
      });
  };
  let handleModal = () => {
    successModalOpen = !successModalOpen;
  };
  let handelaccept = params => {
    push("/#/");
  };
</script>

<style>
  /*custom font*/
  @import url(https://fonts.googleapis.com/css?family=Montserrat);

  * {
    margin: 0;
    padding: 0;
  }
  section {
    height: 100%;
    display: flex;
    justify-content: center;
    /* margin: 100px 0 0 0; */
  }

  /*form styles*/
  #msform {
    width: 600px;
    margin-top: 150px;
    text-align: center;
    position: relative;
  }
  #msform::-webkit-scrollbar {
    display: none;
  }

  #msform .FormContentContainer {
    background: white;
    border: 0 none;
    border-radius: 3px;
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
    padding: 20px 30px;
    box-sizing: border-box;
    width: 80%;
    margin: 0 auto;
    margin-bottom:50px;
  }

  /*Form controlles*/
  #msform .FormControls {
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    margin-top:15px;
  }

  /*headings*/
  .FormTitle {
    font-size: 15px;
    text-transform: uppercase;
    color: #2c3e50;
    margin-bottom: 20px;
  }
  .FormSubTitle {
    font-weight: normal;
    font-size: 13px;
    color: #666;
    margin-bottom: 20px;
  }
  /*progressbar*/
  #progressbar {
    margin-bottom: 30px;
    overflow: hidden;
    /*CSS counters to number the steps*/
    counter-reset: step;
    display: flex;
    justify-content: center;
  }
  #progressbar li {
    list-style-type: none;
    color: white;
    text-transform: uppercase;
    font-size: 9px;
    width: 33.33%;
    float: left;
    position: relative;
  }
  #progressbar li:before {
    content: counter(step);
    counter-increment: step;
    width: 20px;
    line-height: 20px;
    display: block;
    font-size: 10px;
    color: #333;
    background: white;
    border-radius: 3px;
    margin: 0 auto 5px auto;
  }
  /*progressbar connectors*/
  #progressbar li:after {
    content: "";
    width: 100%;
    height: 2px;
    background: white;
    position: absolute;
    left: -50%;
    top: 9px;
    z-index: -1; /*put it behind the numbers*/
  }
  #progressbar li:first-child:after {
    /*connector not needed before the first step*/
    content: none;
  }
  /*marking active/completed steps green*/
  /*The number of the step and the connector before it = green*/
  #progressbar li.active:before,
  #progressbar li.active:after {
    background: #27ae60;
    color: white;
  }
  /* FORM CONTENT */
  .FormInputsContainer {
    /* display: flex;
    flex-direction: column;
    align-items: center */
  }

  .addMemberBTN {
    width: 100%;
    height: 40px;
    border-radius: 5px;
    margin-bottom: 10px;
    transition: all ease 0.3s;
  }
  .addMemberBTN:hover {
    background-color: rgba(26, 169, 122, 1);
  }
  .TeamMemberContainer {
    width: 90%;
    border-radius: 5px;
    height: auto;
    display: block;
    position: relative !important;
    padding: 10px 5px;
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
    margin: 10px auto;
  }
  .TeamMemberContainer h3 {
    margin-bottom: 20px;
  }
  .genderSelect {
    width: 100%;
    border-radius: 5px;
    height: 40px;
    margin-bottom: 30px;
    background-color: white;
  }
</style>

<section>
  <form id="msform">

    <ul id="progressbar">
      <li class:active={ActiveStage >= 0}>Company Overview</li>
      <!-- <li class:active={ActiveStage >= 1}>Team Dynamics</li> -->
      <li class:active={ActiveStage >= 1}>Contact Information</li>
    </ul>

    <div class="FormContentContainer">

      <div class="FormStageContainer">
        <!-- form starts here -->
        {#each Object.keys(formLayout) as stage}
          {#if ActiveStage == stage}
            <div class="FormInputsContainer">
              <div class="FormHeadings">
                <h3 class="FormTitle">{formLayout[stage].title}</h3>
                {#if formLayout[stage].subtitle}
                  <p class="FormSubTitle">{formLayout[stage].subtitle}</p>
                {/if}
              </div>

              
              <!-- form input elements start here -->

              {#each formLayout[stage].elements as inputElement}
                {#if inputElement.type === 'text' || inputElement.type === 'email'}
                  <TextField
                    {...inputElement}
                    bind:value={inputElement.data['value']}
                    bind:isValid={inputElement.data['isValid']} />
                {:else if inputElement.type === 'date'}
                  <DatePicker
                    on:datechange={inputElement.params.onDateChange}
                    bind:chosenDate={inputElement.data['date']}
                    bind:selected={inputElement.data['date']}
                    isAllowed={date => {
                      const millisecs = date.getTime();
                      if (millisecs + 25 * 3600 * 1000 < Date.now()) return true;
                      if (millisecs > Date.now() + 3600 * 24 * 45 * 1000) return false;
                      return true;
                    }} />
                {:else if inputElement.type === 'radio'}
                  <RadioGroup
                    {...inputElement}
                    bind:chosenValue={inputElement.data['value']}
                    bind:isValid={inputElement.data['isValid']} />
                {:else if inputElement.type === 'yes-no'}
                  <YesNoQuestion
                    question={inputElement.params.question}
                    bind:answer={inputElement.data.answer}
                    bind:value={inputElement.data.value}
                    bind:isValid={inputElement.data.isValid} />
                {:else if inputElement.type === 'member'}
                  <div class="TeamMemberContainer">
                    <h3>Member #{inputElement.index}</h3>
                    <TextField
                      checkIsValid={input => {
                        if (validate.isEmpty(input)) {
                          return 'invalid';
                        } else {
                          return 'valid';
                        }
                      }}
                      Name="Name"
                      bind:value={inputElement.name['value']}
                      bind:isValid={inputElement.name['isValid']} />
                    <TextField
                      bind:isValid={inputElement.age['isValid']}
                      checkIsValid={input => {
                        if (validate.isEmpty(input)) {
                          return 'invalid';
                        } else {
                          return 'valid';
                        }
                      }}
                      Name="Age"
                      type="number"
                      bind:value={inputElement['age']['value']} />
                    <select
                      class="genderSelect"
                      bind:value={inputElement['gender']['value']}>
                      <option value="">Gender</option>
                      <option value="Male">Male</option>
                      <option value="Female">Female</option>
                    </select>
                    <TextField
                      bind:isValid={inputElement.city['isValid']}
                      checkIsValid={input => {
                        if (validate.isEmpty(input)) {
                          return 'invalid';
                        } else {
                          return 'valid';
                        }
                      }}
                      Name="Current City"
                      bind:value={inputElement['city']['value']} />
                    <TextField
                      bind:isValid={inputElement.role['isValid']}
                      checkIsValid={input => {
                        if (validate.isEmpty(input)) {
                          return 'invalid';
                        } else {
                          return 'valid';
                        }
                      }}
                      Name="Role Within Team"
                      bind:value={inputElement['role']['value']} />
                    <TextField
                      bind:isValid={inputElement.educationalBackground['isValid']}
                      checkIsValid={input => {
                        if (validate.isEmpty(input)) {
                          return 'invalid';
                        } else {
                          return 'valid';
                        }
                      }}
                      Name="Educational Background"
                      bind:value={inputElement['educationalBackground']['value']} />
                    <TextField
                      multiline
                      bind:isValid={inputElement.workExperience['isValid']}
                      checkIsValid={input => {
                        if (validate.isEmpty(input)) {
                          return 'invalid';
                        } else {
                          return 'valid';
                        }
                      }}
                      Name="Work Experience"
                      bind:value={inputElement['workExperience']['value']} />

                  </div>
                {/if}
              {/each}

              <!-- form input elements end here -->
            </div>

            <div class="FormControls">
              {#if ActiveStage > 0}
                <Button primary text="Previous" on:click={handlePrevious} />
              {/if}
              <Button
                primary
                text={ActiveStage === totalNumberOfStages ? 'Submit' : 'Next'}
                on:click={ActiveStage === totalNumberOfStages ? handleSubmit : handleNext} />
            </div>
            {#if validationerror !== '' && validationerror !== undefined}
              <p>please fill all required fields</p>
            {/if}
          {/if}
        {/each}

        <!-- form ends here -->
      </div>
    </div>

  </form>
</section>
{#if successModalOpen}
  <Modal on:close={handleModal}>
    <SuccessBox on:accept={handelaccept} />
  </Modal>
{/if}
