<script>
  import { onMount } from "svelte";
  import { createEventDispatcher } from "svelte";

  export let type,
    Name,
    value = "",
    label = undefined,
    checkIsValid = () => {
      return true;
    },
    errorMessege = undefined,
    multiline = false,
    notRequired = false,
    isValid = notRequired ? true : false;
  let active,
    inputElement,
    showValidatiuonError = false,
    inputState = "";

  $: showValidatiuonError = inputState === "blur" ? true : false;

  // $: checkIsValid = typeof checkIsValid === 'function' ? checkIsValid : ()=>{}

  onMount(() => {
    // if there is an initial value set the label in active mode
    active = value !== "" && value !== undefined ? true : false;
  });

  let handleFocus = params => {
    inputState = "focus";
    active = true;
  };

  let handleBlur = e => {
    inputState = "blur";
    isValid = checkIsValid(e.target.value);
    if (inputElement.value === "") {
      active = false;
    }
  };

  let handleChange = e => {
    createEventDispatcher()("inputChange", {
      isValid: false,
      value: e.target.value
    });
    isValid = checkIsValid(e.target.value);
    value = event.target.value;
  };
</script>

<style>
  .TextInputContainer {
    text-align: left;
    position: relative;
  }

  .TextInputContainer label {
    position: absolute;
    left: 8px;
    top: 12px;
    color: #999;
    font-size: 16px;
    display: inline-block;
    padding: 0px 10px;
    font-weight: 400;
    background-color: rgba(255, 255, 255, 0);
    pointer-events: none;
    transition: color 0.3s, top 0.3s, background-color 0.3s;
  }
  .TextInputContainer label.active {
    top: -15px;
    color: #555;
    background-color: white;
    border: 1px solid #c6c6c6;
    border-radius: 5px;
  }

  .TextInputContainer input,
  .TextInputContainer textarea {
    padding: 12px;
    font-size: 14px;
    border: 1px solid #c6c6c6;
    width: 100%;
    margin-bottom: 18px;
    color: rgb(61, 60, 60);
    font-family: "Lato", "sans-serif";
    font-size: 16px;
    font-weight: 300;
    background-color: #fff;
    border-radius: 2px;
    transition: all 0.3s;
  }
  .TextInputContainer input:focus,
  .TextInputContainer input:hover {
    outline: none;
    border-color: #9fb1c1;
  }
  .TextInputContainer input:focus + label,
  .TextInputContainer input:hover + label {
    color: #077abc;
    cursor: text;
  }
  .TextInputParagraph {
    text-align: left;
    margin: 0 0 5px 0;
    font-size: 14px;
  }
  .truncate {
    width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .invalid {
    border: 2px solid red !important;
  }
</style>

{#if label}
  <p class="TextInputParagraph" for="">{label}</p>
{/if}
{#if !isValid  && showValidatiuonError && errorMessege}
  <p class="TextInputParagraph" for="">{errorMessege}</p>
{/if}
<div class="TextInputContainer">

  {#if multiline}
    <textarea
      id={Name}
      class="floatLabel"
      name={Name}
      {value}
      rows="4"
      class:invalid={!isValid  && showValidatiuonError}
      on:focus={handleFocus}
      on:blur={handleBlur}
      bind:this={inputElement}
      on:input={handleChange} />
  {:else}
    <input
      {type}
      id={Name}
      class="floatLabel"
      name={Name}
      {value}
      class:invalid={!isValid && showValidatiuonError}
      on:focus={handleFocus}
      on:blur={handleBlur}
      bind:this={inputElement}
      on:input={handleChange} 
      required/>
  {/if}

  <label class="" for={Name} class:active class:truncate={active}>{Name}</label>
</div>
