<script>
  import { createEventDispatcher } from "svelte";
  import Calender from "../common/calendar.svelte";
  import { getMonthName } from "../../scripts/date-time.js";
  import Modal from "../common/modal.svelte";

  const dispatch = createEventDispatcher();

  // props
  export let chosenDate ;
  export let isAllowed = () => true;
  export let selected = new Date();

  // state
  let date, month, year, showDatePicker, showModal;

  // so that these change with props
  $: {
    date = selected.getDate();
    month = selected.getMonth();
    year = selected.getFullYear();
  }

  // handlers
  const onFocus = () => {
    showDatePicker = true;
    showModal = true;
  };

  const next = () => {
    if (month === 11) {
      month = 0;
      year = year + 1;
      return;
    }
    month = month + 1;
  };

  const prev = () => {
    if (month === 0) {
      month = 11;
      year -= 1;
      return;
    }
    month -= 1;
  };

  const oneYearPrev = () => {
    for (let i = 0; i < 12; i += 1) {
      prev();
    }
  };

  const onDateChange = d => {
    showDatePicker = false;
    dispatch("datechange", d.detail);
    chosenDate = d.detail
  };

  let handleModal = () => {
    showModal = !showModal;
  };
</script>

<style>
  .relative {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    align-items: center;
  }
  input {
    width: 80%;
  }
  .box {
    z-index: 20;
    /* position: absolute; */
    /* top: 40px; */
    /* left: 0px; */
    border: 1px solid green;
    display: inline-block;
    background-color: white;
  }

  .month-name {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 6px 0;
  }

  .center {
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>

<div class="relative">

  <label for="">Foundation Date:</label>
  <input type="text" on:click={onFocus} value={selected.toDateString()} />
  {#if showDatePicker && showModal}
    <Modal on:close={handleModal}>

      <div class="box">
        <div class="month-name">
          <div class="center">
            <button on:click|preventDefault={prev}>Prev</button>
          </div>
          <div class="center">{getMonthName(month)} {year}</div>
          <div class="center">
            <button on:click|preventDefault={next}>Next</button>
          </div>
        </div>
        <Calender
          {month}
          {year}
          date={selected}
          {isAllowed}
          on:datechange={onDateChange} />
      </div>

    </Modal>
  {/if}
</div>
