<script>
  import { onMount } from "svelte";

  export let Name,
    value,
    label = undefined;
  let active, inputElement;

  onMount(() => {
    active = value !== "" && value !== undefined ? true : false;
  });

  let handleFocus = params => {
    active = true;
  };
  let handleBlur = params => {
    if (inputElement.value === "") {
      active = false;
    }
  };
</script>

<style>
  .controls {
    text-align: left;
    position: relative;
  }

  .controls label {
    position: absolute;
    left: 8px;
    top: 8px;
    color: #999;
    font-size: 16px;
    display: inline-block;
    padding: 0px 10px;
    font-weight: 400;
    background-color: rgba(255, 255, 255, 0);
    pointer-events: none;
    -moz-transition: color 0.3s, top 0.3s, background-color 0.8s;
    -o-transition: color 0.3s, top 0.3s, background-color 0.8s;
    -webkit-transition: color 0.3s, top 0.3s, background-color 0.8s;
    transition: color 0.3s, top 0.3s, background-color 0.8s;
  }
  .controls label.active {
    top: -15px;
    color: #555;
    background-color: white;
    border: 1px solid #c6c6c6;
    border-radius: 5px;
  }

  .controls input[type="text"] {
    padding: 12px;
    font-size: 14px;
    border: 1px solid #c6c6c6;
    width: 100%;
    margin-bottom: 18px;
    color: #888;
    font-family: "Lato", "sans-serif";
    font-size: 16px;
    font-weight: 300;
    background-color: #fff;
    -moz-border-radius: 2px;
    -webkit-border-radius: 2px;
    border-radius: 2px;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }
  .controls input[type="text"]:focus,
  .controls input[type="text"]:hover {
    outline: none;
    border-color: #9fb1c1;
  }
  .controls input[type="text"]:focus + label,
  .controls input[type="text"]:hover + label {
    color: #077abc;
    cursor: text;
  }
  .textAreaLabel {
    text-align: left;
    margin: 0 0 5px 0;
    font-size: 14px;
  }
  .truncate {
    width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
</style>

{#if label}
  <p class="textAreaLabel" for="">{label}</p>
{/if}
<div class="controls">

  <input
    type="text"
    id={Name}
    class="floatLabel"
    name={Name}
    on:focus={handleFocus}
    on:blur={handleBlur}
    bind:this={inputElement}
    bind:value />
  <label class="" for={Name} class:active class:truncate={active} >{Name}</label>
</div>
