<script>
  import {location as routerLocation , push} from 'svelte-spa-router'
  export let height;
  export let width;
  export let text;
  export let location;
  export let type = "scroll";
  let anchorLink;

  let smoothScroll = function(e) {
    const distanceToTop = el => Math.floor(el.getBoundingClientRect().top);
    e.preventDefault();
    const targetID = anchorLink.getAttribute("href");
    const targetAnchor = document.querySelector(targetID);
    if (!targetAnchor) return;
    const originalTop = distanceToTop(targetAnchor);

    window.scrollBy({ top: originalTop - 50, left: 0, behavior: "smooth" });
  };

  let handleClick = e => {
    if ($routerLocation !== '/'){
      push('/')
      return;
    }
    if(type === 'link' ){
      e.preventDefault();
      push(location)
    }
    if (type === "scroll") {
      smoothScroll(e);
      return;
    } else if (type === "disapled") {
      e.preventDefault();
    }
  };
</script>

<style>
  .animatedLinkTag {
    margin: 0 10px;
    display: block;
  }

  .svg-wrapper {
    height: 60px;
    margin: 0 auto;
    position: relative;
    top: 50%;
    transform: translateY(22%);
    text-align: center;
  }
  .svg-wrapper .animatedLinkText {
    text-align: center;
    width: 100%;
    font-weight: 300;
  }

  .shape {
    fill: transparent;
    stroke-dasharray: 140 540;
    stroke-dashoffset: -474;
    stroke-width: 8px;
    stroke: #19f6e8;
  }
  .animatedLinkText {
    color: #fff;
    font-family: "Roboto Condensed";
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 32px;
    position: relative;
    top: -35px;
  }
 

  @keyframes draw {
    0% {
      stroke-dasharray: 140 540;
      stroke-dashoffset: -474;
      stroke-width: 8px;
    }
    100% {
      stroke-dasharray: 760;
      stroke-dashoffset: 0;
      stroke-width: 2px;
    }
  }
  @keyframes unDraw {
    0% {
      stroke-dasharray: 760;
      stroke-dashoffset: 0;
      stroke-width: 2px;
    }
    100% {
      stroke-dasharray: 140 540;
      stroke-dashoffset: -474;
      stroke-width: 8px;
    }
  }
  .svg-wrapper:hover .shape {
    -webkit-animation: 0.3s draw linear forwards;
    animation: 0.3s draw linear forwards;
    opacity: 1;
  }

  .svg-wrapper .shape {
    -webkit-animation: 0.3s unDraw linear forwards;
    animation: 0.3s unDraw linear forwards;
    opacity: 1;
  }


  svg {
    border-radius: 112px;
  }
  span:hover .svg-wrapper svg {
    transition-duration: 0.3s;
    background-color: #c17fff;
  }
</style>

<li >
  <a class="animatedLinkTag" on:click={handleClick} href='{location}' bind:this={anchorLink}>
    <div class="svg-wrapper">
      <svg {height} {width} xmlns="http://www.w3.org/2000/svg">
        <rect class="shape" {height} {width} rx="15" />
      </svg>
      <div class="animatedLinkText">{text}</div>
    </div>
  </a>
</li>

