<script>

  let showMenuUL,
    ShowMobile = false;

  let closeMenu = ()=>{
      ShowMobile = false;
  }

  let BTNToggleEventListener = function() {
    if (showMenuUL.className.indexOf("active") === -1) {
      ShowMobile = true;
    } else {
      closeMenu()
    }
  };
</script>

<style>
  .menu {
    display: flex;
    align-items: center;
     justify-content: flex-end;
  }

  ul {
    margin: 0 20px 0 0;
    list-style-type: none;
    display: flex;
  }
  /* the mobile nav toggle button  */
  .nav-tgl {
    cursor: pointer;
    position: relative;
    z-index: 100;
    width: 70px;
    height: 70px;
    border: none;
    border-radius: 50%;
    padding: 0;
    background: #fff;
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.24);
    line-height: 0.6;
    text-align: center;
    display: none;
    margin: 0 15px 0 0;
  }



  .active #circle:before {
    visibility: visible;

    clip-path: circle(100%);
  }

  .nav-tgl > span {
    display: inline-block;
    position: relative;
    height: 2px;
    width: 34px;
    border-radius: 1px;
    background: #293335;
    vertical-align: middle;
  }

  .nav-tgl > span:before,
  .nav-tgl > span:after {
    display: inline-block;
    position: absolute;
    content: "";
    height: 2px;
    border-radius: 1px;
    background: #293335;
    transition: all 200ms;
  }

  .nav-tgl > span:before {
    top: -11px;
    left: 3px;
    width: 28px;
  }
  .nav-tgl > span:after {
    top: 11px;
    left: 6px;
    width: 22px;
  }
  .nav-tgl:focus {
    outline: none;
  }
  .nav-tgl:hover > span:after,
  .nav-tgl:hover > span:before {
    width: 34px;
    left: 0;
  }

  .active .nav-tgl > span {
    height: 0;
  }
  .active .nav-tgl > span:after,
  .active .nav-tgl > span:before {
    top: 0px;
    left: 0;
    width: 34px;
  }
  .active .nav-tgl > span:after {
    transform: rotate(-45deg);
  }
  .active .nav-tgl > span:before {
    transform: rotate(45deg);
  }
  #circle:before {
    display: block;
    position: fixed;
    top:0px;
    left: 0;
    content: "";
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    transition: all 500ms ease-in-out;

    clip-path: circle(30px at calc(100% - 50px) 45px);

    visibility: hidden;
  }

  @media screen and (max-width: 767px) {
    ul{
      display: none
    }
    .active ul {
      position: fixed;
      top: 50%;
      left:50%;
      transform: translate(-50%,-50%);
      flex-direction: column;
      display: block;
      z-index: 10;
    }
    .menu {
      justify-content: flex-end;
      align-items: center;
    }
    .nav-tgl {
      display: inline-block;
    }

  }

</style>

<div class="menu" class:active={ShowMobile} bind:this={showMenuUL}>

  <ul class="show-menu" on:click={closeMenu}>
    <slot />
  </ul>

  <button
    class="nav-tgl"
    type="button"
    aria-label="toggle menu"
    on:click={BTNToggleEventListener}>
    <!-- this span just for the three dividers in the hamburger button-->
    <span aria-hidden="true"></span>
  </button>
  <div id="circle" on:click={closeMenu}></div>


</div>
