<script>
  import { onMount } from 'svelte';

  export let Name,
    value,
    label = undefined;
  let active, inputElement;

  onMount(() => {
		active = value !== '' && value !== undefined ? true : false
	});

  let handleFocus = params => {
    active = true;
  };
  let handleBlur = params => {
    if (inputElement.value === "") {
      active = false;
    }
  };
</script>

<style>
  .controls {
    text-align: left;
    position: relative;
  }

  .controls label {
    position: absolute;
    left: 8px;
    top: 8px;
    color: #999;
    font-size: 16px;
    display: inline-block;
    padding: 0 10px !important;
    font-weight: 400;
    background-color: rgba(255, 255, 255, 0);
    pointer-events: none;
    -moz-transition: color 0.3s, top 0.3s, background-color 0.8s;
    -o-transition: color 0.3s, top 0.3s, background-color 0.8s;
    -webkit-transition: color 0.3s, top 0.3s, background-color 0.8s;
    transition: color 0.3s, top 0.3s, background-color 0.8s;
  }
  .controls label.active {
    top: -15px;
    color: #555;
    background-color: white;
    border: 1px solid #c6c6c6;
    border-radius: 5px;
  }
  .controls textarea {
    padding: 15px;
    font-size: 14px;
    border: 1px solid #c6c6c6;
    width: 100%;
    margin-bottom: 18px;
    color: #888;
    font-family: "Lato", "sans-serif";
    font-size: 16px;
    font-weight: 300;
    background-color: #fff;
    -moz-border-radius: 2px;
    -webkit-border-radius: 2px;
    border-radius: 2px;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }

  .controls textarea:focus,
  .controls textarea:hover {
    outline: none;
    border-color: #9fb1c1;
  }
  .controls input[type="text"]:focus + label,
  .controls input[type="text"]:hover + label,
  .controls input[type="email"]:focus + label,
  .controls input[type="email"]:hover + label,
  .controls input[type="tel"]:focus + label,
  .controls input[type="tel"]:hover + label,
  .controls textarea:focus + label,
  .controls textarea:hover + label,
  .controls button:focus + label,
  .controls button:hover + label,
  .controls select:focus + label,
  .controls select:hover + label {
    color: #077abc;
    cursor: text;
  }

  .controls label {
    position: absolute;
    left: 8px;
    top: 12px;
    color: #999;
    font-size: 16px;
    display: inline-block;
    padding: 4px 10px;
    font-weight: 400;
    background-color: rgba(255, 255, 255, 0);
    pointer-events: none;
    -moz-transition: color 0.3s, top 0.3s, background-color 0.8s;
    -o-transition: color 0.3s, top 0.3s, background-color 0.8s;
    -webkit-transition: color 0.3s, top 0.3s, background-color 0.8s;
    transition: color 0.3s, top 0.3s, background-color 0.8s;
  }

  .controls textarea {
    resize: none;
    /* height: 200px; */
    resize: vertical
  }
  .truncate {
    width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .textAreaLabel {
    text-align: left;
    margin: 0 0 5px 0;
    font-size: 14px;
  }
</style>

{#if label}
  <p class="textAreaLabel" for="">{label}</p>
{/if}

<div class="controls">
  <textarea
    name="{Name}"
    class="floatLabel"
    id="{Name}"
    rows="4"
    on:focus={handleFocus}
    on:blur={handleBlur}
    bind:this={inputElement}
    bind:value />
  <label for="{Name}" class:active class:truncate={active}>{Name} </label>
</div>
