<script>
  import TextField from "../Forms/TextField.svelte";
  export let question, value, answer, isValid=false,noSubquestion;

  $: isValid = answer === 'no' && answer !== undefined ? true : 
                noSubquestion ? true : 
                value !== '' && value !== undefined ? true : false


</script>

<style>
  .YNquestion {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .yes,
  .no {
    width: 60px;
    margin-right: 10px;
    transition: all ease 0.3s;
  }
  .yes:hover {
    background-color: rgba(26, 169, 122, 1);
  }
  .no:hover {
    background-color: #dc3545;
  }
  .btn-container {
    display: flex;
    justify-content: flex-end;
  }
</style>

<div class="YNquestion">
  <p>{question}</p>
  <div class="btn-container">
    <button
      class="yes"
      on:click|preventDefault={() => {
        answer = 'yes';
      }}>
      YES
    </button>
    <button
      class="no"
      on:click|preventDefault={() => {
        answer = 'no';
      }}>
      NO
    </button>
  </div>

</div>
{#if answer === 'yes' && !noSubquestion }
  <TextField
    Name="How much money has been raised since the start?"
    bind:value />
{/if}
