<script>
  import HeaderLogo from "./headerLogo.svelte";
  import ResponsiveMenu from "./responsiveMenu.svelte";
  import HeaderLink from "./headerLink.svelte";
  import AnimatedButton from "../common/animatedButton.svelte";
  import Button from "../common/Button.svelte";
  import Fancy from "../common/fancySelectComponent.svelte";
  import { push, location } from "svelte-spa-router";

  import Modal from "../common/modal.svelte";

  

  let backgroundColor = false , enableChangeOnScroll = true;
  let showModal = false;
  $: backgroundColor = $location !== "/"  ? true:false 
  $: enableChangeOnScroll = $location !== "/" ? false:true 
  

  let changeNavBackgroundOnScroll = event => {
    if (!enableChangeOnScroll) { return }
    const windowTop = window.pageYOffset;
    windowTop > 100 ? (backgroundColor = true) : (backgroundColor = false);
  };

  let handleModal = () => {
    showModal = !showModal;
    //  prevent scrolling when modal is opened

    if (showModal) {
      document.body.style.overflowY = "hidden";
    } else {
      document.body.style.overflowY = "unset";
    }
  };

  let handleCompetetionFormRedirectionMid = () => {
    handleModal();
    push("/competition-registration");
  };
  let handleCompetetionFormRedirectionTop = () => {
    handleModal();
    push("/mutex-form");
  };
  let handleCompetetionFormRedirectionBot = () => {
    handleModal();
    push("/workshops-Form");
  };
  let handleNavTransparency = () => {
    return $location === "/competition-registration" ? true : backgroundColor;
  };
</script>

<style>
  header {
    width: 100vw;
    height: auto;
    background: rgba(255, 255, 255, 0);
    display: flex;
  }

  nav {
    width: 100vw;
    height: 100px;
    background-color: #ffffff00;
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: fixed;
    z-index: 10;
    transition: all 0.3s;
    height: 100px;
  }
  .navBackgroundColor {
    background-color: #a13dff;
  }
</style>

<svelte:window on:scroll={changeNavBackgroundOnScroll} />
<header>
  <nav class:navBackgroundColor={backgroundColor}>
    <HeaderLogo />
    <ResponsiveMenu>
      <HeaderLink
        location={'#aboutSection'}
        width={75}
        height={30}
        text={'ABOUT'} />
      <HeaderLink
        location={'#speakers'}
        width={100}
        height={30}
        text={'SPEAKERS'} />
      <HeaderLink
        location={'#sponsors'}
        width={100}
        height={30}
        text={'PARTNERS'} />
      <HeaderLink
        location={'/exhibition-Form'}
        width={85}
        height={30}
        text={'EXHIBIT'}
        type='link'
         />
      <HeaderLink
        location={'#'}
        width={85}
        height={30}
        text={'AGENDA'}
        type="disapled" />
      <!-- <HeaderLink location={'#'} width={95} height={30} text={'REGISTER'} /> -->
      <AnimatedButton arrow text="REGISTER" on:click={handleModal} />
    </ResponsiveMenu>
  </nav>

</header>

{#if showModal}
  <Modal on:close={handleModal}>
    <h2 slot="header">Register as ?</h2>
    <span slot="controls">
      <Button text="Choose" />
    </span>

    <Fancy on:clickMid={handleCompetetionFormRedirectionMid} 
    on:clickLeft={handleCompetetionFormRedirectionTop}
    on:clickRight={handleCompetetionFormRedirectionBot}/>
  </Modal>
{/if}
