<script>
  import { fade } from "svelte/transition";
  // elements
  import TextField from "../common/FormTextInput.svelte";
  import Modal from "../common/modal.svelte";
  import SuccessBox from "../common/SuccessBox.svelte";
  import Button from "../common/Button.svelte";
  import WorkshopsCheckBox from "./workshopsCheckBox.svelte";
  //  meta
  import { onMount, onDestroy } from "svelte";
  import { Firestore } from "../../config/firebase.js";
  import { push } from "svelte-spa-router";
  import validate from "validate.js";
  // stores

  let successModalOpen, validationerror;

  let items = [
    {
      value: "Blockchain",
      description: `
        <p>Pre-Requisites:
        <ol>
        <li> Basic understanding of WEB
        architecture (HTTP, REST).</li>

        <li> Basic knowledge of HTML.</li>
        <li> Basic knowledge of JavaScript.</li>

        <li> Have an understanding of Unix
        commands and NodeJS.</li>
        </ol>
        </p>
    `
    },
    "AR/VR",

    "IOT",

    "Cyber Security",

    "Intellectual Property Rights",

    "Startups Legal Issues",

    "Shareholders agreement & termsheet",

    "Digital Fabrication",

    "AR",

    "Deep Learning",

    "How to get investment ready",

    "How to manage your Startup"
  ];
  let formData = {
    stagemap: {
      0: "companyInfo",
      1: "contactInfo"
    },
    companyInfo: {},
    contactInfo: {}
  };

  let isValid = false,
    chosenValues,
    Email,
    isFormValid;

  $: isFormValid = Email !== "" && isValid ? true : false;
  $: console.log(chosenValues);
  // event handlers and lifecycle methods
  onMount(() => {
    document.querySelector("html").style.backgroundColor =
      " rgb(129, 111, 131)";
  });
  onDestroy(() => {
    document.querySelector("html").style.backgroundColor = "";
  });

  let handleSubmit = async () => {
    if (Email === "") {
      validationerror = " Please enter your Email!";
      return;
    }
    if (!isValid) {
      validationerror = " Please Choose at least 1 workshop! ";
      return;
    }
    let EmailValid = false;
    await Firestore.collection("mutexattendes")
      .where("info.Email", "==", Email) // <-- This line
      .get()
      .then(function(querySnapshot) {
        console.log(querySnapshot, querySnapshot.empty ? false : true);
        EmailValid = querySnapshot.empty ? false : true;
      });

    if (!EmailValid) {
      validationerror =
        " your Email doesn't exit in our Database , Did you sign up for the event itself ? ";
      return;
    }

    let collection = Firestore.collection("workshops");
    collection
      .add({ Email: Email, workshopsChosen: chosenValues })
      .then(function() {
        console.log("Document successfully written!");
        successModalOpen = true;
      })
      .catch(function(error) {
        console.error("Error writing document: ", error);
      });
  };
  let handleModal = () => {
    successModalOpen = !successModalOpen;
  };
  let handelaccept = params => {
    push("/#/");
  };
</script>

