    <div class="FormContentContainer">

      <div class="FormStageContainer">
        <!-- form starts here -->
        <TextField
          Name="Email"
          type="email"
          errorMessege="This field is required."
          bind:value={Email} />
        <!-- form input elements start here -->

        <!-- form input elements end here -->
        <WorkshopsCheckBox
          {items}
          Name="Choose the workshops you would like to attempt."
          bind:isValid
          bind:chosenValues />

        <div class="FormControls">

          <Button primary text={'Submit'} on:click={handleSubmit} />
        </div>
